@tailwind base;
@tailwind components;
@tailwind utilities;

h1, h4 {
    font-family: 'DM Serif Display', serif;
}

p {
    color: #5D6167;
    font-family: "Work Sans", sans-serif;
    text-align: center;
    font-weight: bold
}
.card-title {
    font-family: 'DM Serif Display', serif;
    color: #2B2F38;
}

img {
    object-fit: fill;
}

.card-text {
    color: #5D6167;
}

button {
	background-color: #FC5F5F;
	border: 1px solid #FC5F5F;
	border-radius: 50px;
	padding: 3px 36px;
	color: #F9F9F9;
	font-family: 'Work Sans', sans-serif;
	font-weight: 400;
}

button:hover {
	background-color: #F9F9F9;
	border: 1px solid #FC5F5F;
	color: #FC5F5F;
}
html {
    font-size: 18px;
}

/* hero image */
#hero {
    width: 100%;
    height: 100vh;
    /* shorthand background: linear-gradient, url, background-repeat, background-position / background-size */
    /* use linear-gradient to add a darken background effect to the image to make the text easier to read - make sure to click the box to change the opacity */
    background: linear-gradient(rgba(255, 255, 255, 0.36), rgba(255, 255, 255, 0.161)), url("https://images.pexels.com/photos/4600309/pexels-photo-4600309.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2") no-repeat center / cover;
}

/* contains everything inside the hero image */
#hero .container {
    width: 90%;
    height: 100%;
    /* limit the width on bigger devices */
    max-width: 1000px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    /* makes them line up on the right side (horizontal) */
    justify-content: right;
    /* text-align makes everything go to the right side */
    text-align: right;
    color: #F9F9F9;
    text-shadow: -6px -3px 22px #5e5757;
}

/* since we set the HTML font-size to 18px the rem is the value we multiply this by (em is just for the container we set it in) */
/* for the heading */
#hero .container .info h1 {
    font-size: 3rem;
}

#hero .container .info h4 {
    font-size: 1.5rem;
}
#about a {
    color: #FC5F5F;
}

#about a:hover {
    color: #F9F9F9;
    background-color: #FC5F5F;
    padding: 5px 5px;
    border-radius: 50px;
    font-weight: bold;
    text-decoration: none;
}

 a#link {
    color: #FC5F5F;
}

a#link:hover {
    color: #F9F9F9;
